@import url('https://fonts.googleapis.com/css2?family=Hedvig+Letters+Serif:opsz@12..24&display=swap');


body {
  background-color: #EBEDDF;
}
* {
  margin: 0;
  font-weight: 400;
  font-family: "Hedvig Letters Serif", sans-serif;
}
.navbar {
  display: grid;
  padding-top: 15px;
  padding-bottom: 15px;
  align-items: center;
  background-color: #384b2d;
  width: 100%;
}
.navbar .links {
  text-align: center;
}
.navbar a {
  text-decoration: none;
  color: #EBEDDF;
  font-weight: 800;
  padding: 6px;
}
.navbar a:hover{
  color: #86679a;
}
.navbar .icons{
  margin-left: auto;
}
