@import url('https://fonts.googleapis.com/css2?family=Hedvig+Letters+Serif:opsz@12..24&display=swap');
.mobile{
  display: none;
}
.container {
  display: grid;
  grid-template-columns: repeat(14,1fr);
  grid-template-rows: repeat(12,50px);
}
.info{
  color:#0d0d0d;
  grid-column-start: 5;
  grid-column-end: 10;
  grid-row-start: 6;
  z-index: 2;
}
.info h1{
  font-weight: 1000;
  font-size: 360%;  
}
.info p{
  padding-top: 10px;
  font-size: 120%;  
}
.pfp {  
  grid-column-start: 10;
  margin-top: 10%;
  grid-row-start: 4;
  z-index: 2;
  width: 200%;
  border-radius: 150px;
  border: 5px solid #384b2d;  
}

.icons {  
  color: #86679a;
  grid-column-start: 6;
  grid-column-end: 8;
  grid-row-start: 9;
  z-index: 2;
}
.icons:hover{
  color: #86679a;
}
.container1 {
  display: inline-flex;
  justify-content: center;
  grid-column-start: 1;
  grid-column-end: 7;
  grid-row-start: 2;
  grid-template-columns: 200px 200px 200px 100px 100px;
  grid-template-rows: 30px 20px;
  width: 30px;
  height: 30px;
  padding: 10px;
  border-radius: 10%;
  box-shadow: 3px 10px 3px rgb(153, 153, 153);
  background-color: #ffffff;
  margin-right: 30px;
}
.container1:hover{
  background-color: #637d55;
}
@media (max-width: 430px) {
  .container {
    display: none;
  }
  .mobile{
    display: block;
  }
  .infoM{
    color:#0d0d0d;
    text-align: center;
    margin-top: 20%;
  }
  .infoM h1{
    font-weight: 1000;
    font-size: 200%;  
  }
  .infoM p{
    padding-top: 10px;
    font-size: 80%;  
    text-align: center;
    margin-left: 15%;
    margin-right: 15%;
  }
  .pfpM {  
    margin-top: 7%;
    z-index: 2;
    width: 50%;
    border-radius: 150px;
    border: 5px solid #384b2d;  
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .iconsM{
    /* display: none; */
    color: #86679a;
    z-index: 2;
    display: block;
    margin-left: 35%;
    margin-right: auto;
    margin-top: 10%;
  }
  .iconsM:hover{
    color: #86679a;
  } 
  .containerM {
    margin-right: 30px;
    width: 10px;
    height: 10px;
    padding: 10px;
    border-radius: 10%;
    box-shadow: 3px 10px 3px rgb(153, 153, 153);
    background-color: #ffffff;
  }
  .containerM:hover {
    background-color: #637d55;
  }
}